<template>
  <div>
    <div
      class="villain-block-wrapper">
      <VillainPlus
        @add="$emit('add', $event)"
        @move="$emit('move', $event)"
      />
    </div>

    <transition-group name="bounce">
      <div
        v-for="b in cBlocks"
        :key="b.uid"
        class="villain-block-container">
        <component
          :is="b.type + 'Block'"
          :block="b"
          @add="$emit('add', $event)"
          @delete="$emit('delete', $event)"
          @move="$emit('move', $event)"
        />
      </div>
    </transition-group>
  </div>
</template>

<script>

export default {
  name: 'BlockContainer',

  props: {
    blocks: {
      type: Array,
      default: () => []
    }
  },

  data () {
    return {
      uid: null
    }
  },

  computed: {
    cBlocks: {
      get: function () {
        return [...this.blocks]
      },
      set: function (blocks) {
        this.$emit('order', blocks)
      }
    }
  },

  created () {
    console.debug('<BlockContainer /> created')
    this.uid = (Date.now().toString(36) + Math.random().toString(36).substr(2, 5)).toUpperCase()
  }
}
</script>
